<template>
  <div>
    <!-- loop in activity -->
    <!-- responsive table -->
    <div class="table-responsive m-4">
      <table class="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th v-for="tableHeader in tableHeaders" :key="tableHeader">
              {{ tableHeader }}
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in activityMsg" :key="index">
            <th scope="row">{{ activityMsg.length - index }}</th>
            <td>{{ item.date }}</td>
            <td>
              <a :href="item.link"> {{ item.title }} </a>
            </td>
            <td>{{ item.proeqti }}</td>
            <td>
              <a :href="item.organizatorLink">{{ item.organizator }}</a>
            </td>
            <td>{{ item.comment }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { firebase } from "@firebase/app";
import "firebase/firestore";

function sortBy(arr, key) {
  return arr.sort(function (a, b) {
    if (a[key] < b[key]) return -1;
    if (a[key] > b[key]) return 1;
    return 0;
  });
}

export default {
  data() {
    return {
      titleText: "2012-დღემდე",
      activityMsg: [],

      activityMsgLength: null,
      actSorted: [],
      tableHeaders: [
        "თარიღი",
        "აქტივობა",
        "პროექტი",
        "ორგანიზატორი",
        "კომენტარი",
      ],
    };
  },
  mounted() {
    this.fetchMessages();
    this.sortBy("id");
  },

  methods: {
    async fetchMessages() {
      const db = firebase.firestore();
      const messageSnap = await db.collection("activityMsg").get();
      this.activityMsg = messageSnap.docs.map((p) => p.data());
      console.log(this.activityMsg);
      this.activityMsgLength = this.activityMsg.length;
    },
    sortBy: function (key) {
      sortBy(this.activityMsg, key);
    },
  },
};
</script>
